import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import cx from 'classnames';
import s from './Simple.scss';
import { Formik } from 'formik';
import Input from '../Input/Input';
import Container from 'components/Container';
import Button from 'components/Button';
import Title from 'components/Title';
import Para from 'components/Para';
import Radio from '../Radio';
import RadioGroup from '../RadioGroup';
import Validator from 'modules/Validator';
import Api from 'modules/Api';
import * as cons from '../../constants';
import { notification, message } from 'antd';
import messageS from 'antd/lib/message/style/index.css';
import Tooltip from 'components/Tooltip';
import Rules from '../Rules';


const schema = Validator.object().shape({
  fio: Validator.string().required('    '),
  phone: Validator.string()
    .phone(' ')
    .required('  '),
  rules: Validator.boolean().oneOf([true], ' '),
});

const Simple = props => (
  <div>
    <Formik
      validateOnChange={false}
      validationSchema={schema}
      onSubmit={(values, {setSubmitting, setErrors, resetForm}) => {
        Api.actions
          .api({
            url: cons.simple_form_api,
            params: {
              method: 'post',
              body: {
                contactName: values.fio,
                phone: values.phone,
                type: values.type,
              },
            },
          })
          .then(data => {
            setSubmitting(false);
            notification.success({
              message: '  ',
              duration: 5,
            });
            resetForm();
          })
          .catch(err => {
            setSubmitting(false);
            notification.error({
              message: ' ',
              description: '     !!!!',
              duration: 5,
            });
          });
      }}
      initialValues={{
        fio: '',
        phone: '',
        type: 1,
        rules: false,
      }}
      render={({
                 values,
                 errors,
                 setFieldValue,
                 handleSubmit,
                 isSubmitting,
               }) => {
        return (
          <div
            id="simpleOrder"
            className={cx([s.root])}>
            <Container>
              <Title type="h3" classes={{root: s.title}}>
                 
              </Title>
              <Para className={s.para}>
                       
              </Para>
              <div className={s.radioGroup}>
                <span className={s.label}>  :</span>
                <RadioGroup
                  name="type"
                  value={values.type}
                  defaultValue={1}
                  onChange={ev => {
                    setFieldValue('type', ev.target.value)
                  }}
                >
                  <div onClick={() => setFieldValue('type', 1)}>
                    <Radio className={s.radio} value={1}>
                      
                    </Radio>
                  </div>
                  <div onClick={() => setFieldValue('type', 2)}>
                    <Radio className={s.radio} value={2}>
                       
                    </Radio>
                  </div>
                  <div onClick={() => setFieldValue('type', 3)}>
                    <Radio className={s.radio} value={3}>
                       
                    </Radio>
                  </div>
                </RadioGroup>
              </div>
              <span className={s.label}> :</span>
              <div className={s.inputGroup}>
                <Tooltip title={errors.fio} visible={errors.fio}>
                  <Input
                    value={values.fio}
                    placeholder="  "
                    name="fio"
                    onChange={ev => setFieldValue('fio', ev.target.value)}
                  />
                </Tooltip>
              </div>
              <div className={s.inputGroup}>
                <Tooltip
                  visible={errors.phone}
                  title={errors.phone}>
                  <Input
                    value={values.phone}
                    onChange={ev => setFieldValue('phone', ev.target.value)}
                    name="phone"
                    placeholder=""
                    mask={[
                      '+',
                      '7',
                      '(',
                      /[1-9]/,
                      /\d/,
                      /\d/,
                      ')',
                      ' ',
                      /\d/,
                      /\d/,
                      /\d/,
                      '-',
                      /\d/,
                      /\d/,
                      /\d/,
                      /\d/,
                    ]}
                  />
                </Tooltip>
              </div>
              <div className={s.inputGroup}>
                <Button
                  fullWidth
                  type="submit"
                  onClick={handleSubmit}
                  isLoading={isSubmitting}
                >
                   
                </Button>
              </div>
              <Rules
                onClick={() => setFieldValue('rules', !values.rules)}
                name="rules"
                isError={errors.rules}
                onChange={(ev) => setFieldValue('rules', ev.target.checked)}
                checked={values.rules}
              />
            </Container>
          </div>
        );
      }}
    />

  </div>
);

export default withStyles(messageS, s)(Simple);



